<template> 
    <example title="Radio Button" :exampleCode="radioCode">
        <semantic-radiobutton label="Radio Button"
                              v-model="model"
                              :data="options"
                              :disabled="disabled"
                              :type="type"
                              ></semantic-radiobutton>

        <semantic-divider hidden="true"></semantic-divider>

        <div class="ui form">
            <h4>Types</h4>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Toggle" v-model="toggle"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Slider" v-model="slider"></semantic-checkbox>
                </div>
            </div>
            <div class="field">
                <semantic-checkbox type="slider" label="Disabled" v-model="disabled"></semantic-checkbox>
            </div>
        </div>                                 
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'radioButton',
            options: [
                { label: 'One', value: 'One' },
                { label: 'Two', value: 'Two' },
                { label: 'Three', value: 'Three' },
            ],
            model: 'One',
            toggle: false,
            slider: false,
            disabled: false,
        }
    },
    computed: {
        radioCode() {
            return `<semantic-radiobutton label="label" :data="yourdataArray" v-model="yourModelProperty" ${this.type ? 'type="'+this.type+'"' : ''}${this.disabled ? ' :disabled="true"' : ''}></semantic-radiobutton>` // eslint-disable-line
        },
        type() {
            return `${!this.toggle && !this.slider ? 'radio' : ''}${this.toggle ? 'toggle' : ''}${this.slider ? 'slider' : ''}` // eslint-disable-line
        },
    },
    watch: {
        toggle() {
            if (this.toggle) this.slider = false
        },
        slider() {
            if (this.slider) this.toggle = false
        },
    },
}
</script>



