<template> 
    <example title="Loading" :exampleCode="loadingCode">
        <div class="ui segment" :class="{'inverted' : inverted}">           
            <semantic-loading :inline="inline" 
                              :centered="centered" 
                              :inverted="inverted" 
                              :text="getText">
            </semantic-loading>
        </div>
    
        <div class="ui form">
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Inline" v-model="inline"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Centered" v-model="centered"></semantic-checkbox>
                </div>
            </div>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Inverted" v-model="inverted"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Show Text" v-model="text"></semantic-checkbox>
                </div>
            </div>
        </div>
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'loading',
            inline: false,
            inverted: false,
            centered: false,
            text: true,
        }
    },
    computed: {
        loadingCode() {
            return `<semantic-loading${!this.text ? ' text=""' : ''}${this.inline ? ' :inline="true"' : ''}${this.centered ? ' :centered="true"' : ''}${this.inverted ? ' :inverted="true"' : ''}></semantic-loading>` // eslint-disable-line
        },
        getText() {
            return `${this.text ? 'Loading...' : ''}`
        },
    },
}

</script>

<style scoped>
    div .ui .segment {
        min-height: 90px;
    }
</style>



