<template> 
    <example title="Divider" :exampleCode="dividerCode">
        <semantic-divider :class="type"></semantic-divider>

        <semantic-divider class="hidden"></semantic-divider>
        <div class="ui form">
            <h4>Types</h4>
            <div class="ui stackable two column grid">
                <div class="column">
                    <semantic-radiobutton v-model="type"
                                          :data="types"
                                          type="slider"
                    ></semantic-radiobutton>
                </div>
            </div>
        </div>
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'sem-label',
            tag: false,
            type: '',
            types: [
                { label: 'Standard', value: '' },
                { label: 'Fitted', value: 'fitted' },
                { label: 'Header', value: 'header' },
                { label: 'Hidden', value: 'hidden' },
                { label: 'Horizontal', value: 'horizontal' },
                { label: 'Inverted', value: 'inverted' },
                { label: 'Section', value: 'section' },
                { label: 'Vertical', value: 'vertical' },
            ],
        }
    },
    computed: {
        dividerCode() {
            return `<semantic-divider${this.type ? ' class="'+this.type+'"' : ''}></semantic-divider>` // eslint-disable-line
        },
    },
}
</script>



