<template> 
    <example title="Pagination" :exampleCode="paginationCode">
            <semantic-pagination 
                                :current-page="1"
                                :total-pages="10"
                                :totalItems="100">
            </semantic-pagination>
        </div>                                 
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'statistic',
        }
    },
    computed: {
        paginationCode() {
            return `coming soon` // eslint-disable-line
        },
    },
}

</script>



