<template> 
    <example title="Statistic" :exampleCode="statisticCode">
        <semantic-statistic label="">
        Boo!
        </semantic-statistic>                                
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'statistic',
        }
    },
    computed: {
        statisticCode() {
            return `<semantic-statistic label=""><!-- Your Statistic Here --></semantic-statistic>` // eslint-disable-line
        },
    },
}

</script>



