<template> 
    <example title="Column" :exampleCode="columnCode">
        <div class="ui padded grid">
            <semantic-column :width="width" class="centered column black"></semantic-column>
        </div>

        <semantic-divider class="hidden"></semantic-divider>
        
        <div class="ui form">
            <div class="field">
                <label>Enter a width between one and sixteen</label>
                <div class="ui right labeled input">
                    <input name="width" type="text" placeholder="width" v-model="width">
                    <div class="ui basic label">wide</div>
                </div>
            </div>
        </div>
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'column',
            width: 'ten',
        }
    },
    computed: {
        columnCode() {
            return `<semantic-column${this.width ? ' width="'+this.width+'"' : ''}></semantic-column>` // eslint-disable-line
        },
    },
}
</script>



