<template>
    <example title="Form Dropdown" :exampleCode="formDropdownCode">
        <semantic-form-dropdown v-if="!search"
                                class="example"
                                value="example"
                                placeholder="Placeholder text" 
                                v-model="dropDown"
                                :options="options"
                                :fluid="fluid"
                                :inline="inline"
                                :multiple="multiple"
                                :disabled="disabled"
                                :allow_additions="false">
        </semantic-form-dropdown>
        <semantic-form-dropdown v-if="search"
                                class="example"
                                value="example"
                                placeholder="Placeholder text" 
                                v-model="dropDown"
                                :options="options"
                                :fluid="fluid"
                                :inline="inline"
                                :multiple="multiple"
                                :disabled="disabled"
                                :search="true"
                                :full_text_search="full_text_search"
                                :allow_additions="false">
        </semantic-form-dropdown>
        
        <div class="ui form">
            <h4>Types</h4>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Fluid" v-model="fluid"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Inline" v-model="inline"></semantic-checkbox>
                </div>
            </div>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Multiple Selction" v-model="multiple"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Disabled" v-model="disabled"></semantic-checkbox>
                </div>
            </div>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Search" v-model="search"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Full Text Search" v-model="full_text_search"></semantic-checkbox>
                </div>
            </div>
        </div>
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'formDropdown',
            placeholder: 'Form Dropdown',
            dropDown: '',
            fluid: true,
            inline: false,
            multiple: false,
            disabled: false,
            search: false,
            full_text_search: false,
            options: [
                { id: 1, name: 'one' },
                { id: 2, name: 'two' },
                { id: 3, name: 'three' },
            ],
            someValue: 1,
        }
    },
    computed: {
        formDropdownCode() {
            return `<semantic-form-dropdown placeholder="Form Dropdown" :options="yourOptionsArray" :value="yourValue"${this.search ? ':search="true"' : ''}${this.full_text_search ? ' :full_text_search="true"' : ''}${this.fluid ? ' :fluid="true"' : ''}${this.inline ? ' :inline="true"' : ''}${this.multiple ? ' :multiple="true"' : ''}${this.disabled ? ' :disabled="true"' : ''}></semantic-form-dropdown>`
        },
    },
    watch: {
        mutliple() {
            this.dropDown = ''
        },
    },
}

</script>



