<template>
    <example title="Search" :exampleCode="searchCode">
        <semantic-search :placeholder="placeholder" 
                         :name="name"
                         :fluid="fluid"
                         :action="action"
                         :icon="icon"
                         :disabled="disabled"
                         :url="search.url" 
                         :fields="search.fields"
                         :local="options"> 
            <button v-if="action" class="ui button">Add</button>
            <i v-if="icon" class="search icon"></i>
        </semantic-search>

        <semantic-divider hidden="true"></semantic-divider>
        
        <div class="ui form">
            <h4>Types</h4>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Fluid" v-model="fluid"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Action" v-model="action"></semantic-checkbox>
                </div>
            </div>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Icon" v-model="icon"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Disabled" v-model="disabled"></semantic-checkbox>
                </div>
            </div>
        </div>
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'Search',
            placeholder: 'Search..',
            fluid: false,
            action: false,
            icon: true,
            disabled: false,
            search: {
                url: '',
                fields: {
                    results: 'data',
                    title: 'name',
                },
            },
            options: {
                data: [
                    { id: 1, name: 'one' },
                    { id: 2, name: 'two' },
                    { id: 3, name: 'three' },
                ]
            },
            actionCode: '<button class="ui button">Action</button>',
            iconCode: '<i class="search icon"></i>',

        }
    },
    computed: {
        searchCode() {
            return `<semantic-search ${this.fluid ? ':fluid="true"' : ''}${this.action ? ' :action="true"' : ''}${this.icon ? ' :icon="true"' : ''}${this.disabled ? ' :disabled="true"' : ''}>${this.action ? this.actionCode : ''}${this.icon ? this.iconCode : ''}</semantic-search>`
        },
    },
    watch: {
        icon() {
            if (this.icon) this.action = false
        },
        action() {
            if (this.action) this.icon = false
        },
    },
}

</script>



