<template>
    <example title="Modal" :exampleCode="modalCode">
        <div class="ui basic button center algined" @click="showModal = !showModal">Show Modal</div>

        <semantic-modal v-model="showModal" 
                        title="test"
                        :settings="{ 'closable': true, 'closable_button': true }"
                        :size="size">
                <div class="ui center aligned basic segment"><h4>Hello World!</h4></div>
        </semantic-modal>

        <semantic-divider hidden="true"></semantic-divider>

        <div class="ui form">
            <h4>Types</h4>
            <div class="ui stackable two column grid">
                <div class="column">
                    <semantic-radiobutton label="Radio Button"
                                          v-model="size"
                                          :size="size"
                                          :data="sizes"
                                          type="slider"
                    ></semantic-radiobutton>
                </div>
                <div class="column">
                    <div class="grouped fields">
                        <div class="field">
                            <semantic-checkbox type="slider" label="Closable" v-model="closable"></semantic-checkbox>
                        </div>
                        <div class="field">
                            <semantic-checkbox type="slider" label="Show Closable Button" v-model="closable_button"></semantic-checkbox>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'modal',
            sizes: [
                { label: 'Small', value: 'small' },
                { label: 'Large', value: 'large' },
                { label: 'Full Screen', value: 'fullscreen' },
            ],
            size: 'small',
            closable: true,
            closable_button: true,
            active: true,
            showModal: false,
            options: [
                { id: 1, name: 'one' },
                { id: 2, name: 'two' },
                { id: 3, name: 'three' },
            ],
            someValue: 1,
        }
    },
    computed: {
        modalCode() {
            return `<semantic-modal title="title" v-model="yourShowModalProperty" ${this.size ? 'size="'+this.size+'"' : ''}${!this.closable || !this.closable_button ? this.settings : ''}><!-- content goes here --></semantic-modal>` // eslint-disable-line
        },
        settings() {
            return ' :settings="{closable: '+this.closable+', closable_button: '+this.closable_button+'}"' // eslint-disable-line
        },
    },
    methods: {
        setSize(value) {
            this.size = value
        },
    },
}

</script>



