<template> 
    <example title="Checkbox" :exampleCode="checkboxCode">
        <semantic-checkbox label="Checkbox"
                            :type="type"
                            :disabled="disabled"
                            v-model="model">
        </semantic-checkbox>
       
       <semantic-divider hidden="true"></semantic-divider>

        <div class="ui form">
            <h4>Types</h4>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Toggle" v-model="toggle"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Slider" v-model="slider"></semantic-checkbox>
                </div>
            </div>
            <div class="field">
                <semantic-checkbox type="slider" label="Disabled" v-model="disabled"></semantic-checkbox>
            </div>
        </div>                                 
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'checkbox',
            model: true,
            toggle: false,
            slider: false,
            disabled: false,
        }
    },
    computed: {
        checkboxCode() {
            return `<semantic-checkbox label="Label" v-model="yourModelProperty" ${this.type ? 'type="'+this.type+'"' : ''}${this.disabled ? ' :disabled="true"' : ''}></semantic-checkbox>` // eslint-disable-line
        },
        type() {
            return `${!this.toggle && !this.slider ? 'checkbox' : ''}${this.toggle ? 'toggle' : ''}${this.slider ? 'slider' : ''}` // eslint-disable-line
        },
    },
    watch: {
        toggle() {
            if (this.toggle) this.slider = false
        },
        slider() {
            if (this.slider) this.toggle = false
        },
    },
}
</script>



