<template>
    <example title="Accordion" :exampleCode="accordionCode">
        <semantic-accordion :styled="styled" :fluid="fluid" :items="accordionItems" class="example"></semantic-accordion>
            <div class="ui form">
                <div class="grouped fields">
                    <h4>Types</h4>
                    <div class="field">
                        <semantic-checkbox type="slider" label="Standard / Styled (prefered)" v-model="styled"></semantic-checkbox>
                    </div>
                    <div class="field">
                      <semantic-checkbox type="slider" label="Fluid" v-model="fluid"></semantic-checkbox>
                    </div>
                </div>
            </div>
        </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            accordionItems: [
              { title: 'SCALABILITY & STRATEGY', content: 'We have over 1,000 experts on our Network. They give us the ability to scale up or down depending on our clients requirements at a moments notice.' },
              { title: 'OUR CROUDIES', content: 'Are a talented bunch. We have sourced the top experts in PPC, SEO, Content, Display, Programmatic and Paid Social from around the world.' },
            ],
            styled: true,
            fluid: true,
        }
    },
    computed: {
        accordionCode() {
            return `<semantic-accordion :items="yourItemArray"${this.fluid ? '' : ' :fluid="false"'}${!this.styled ? ' :styled="false"' : ''}></semantic-accordion>`
        },
    },
}
</script>



