<template> 
    <example title="Reveal" :exampleCode="revealCode">
    <semantic-reveal>
            <div slot="visible"><img src=".././assets/logo.png" class="ui small image"></div>
            <div slot="hidden"><img src=".././assets/logo2.png" class="ui small image"></div>
    </semantic-reveal>         
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'reveal',
        }
    },
    computed: {
        revealCode() {
            return `<semantic-reveal><div slot="visible"><!-- Visisble Content Here --></div><div slot="hidden"><!-- Hidden Content Here --></div></semantic-reveal>` // eslint-disable-line
        },
    },
}

</script>



