<template> 
     <example title="Label" :exampleCode="labelCode">
        <semantic-label :class="type">Hello</semantic-label>
    
        <semantic-divider hidden="true"></semantic-divider>

        <div class="ui form">
            <h4>Types</h4>
            <div class="ui stackable two column grid">
                <div class="column">
                    <semantic-radiobutton v-model="type"
                                          :data="types"
                                          type="slider"
                    ></semantic-radiobutton>
                </div>
            </div>
        </div>
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'sem-label',
            tag: false,
            type: 'ribbon',
            types: [
                { label: 'Pointing', value: 'pointing' },
                { label: 'Corner', value: 'corner' },
                { label: 'Ribbon', value: 'ribbon' },
                { label: 'Attached', value: 'attached' },
                { label: 'Basic', value: 'basic' },
                { label: 'Circular', value: 'circular' },
                { label: 'Floating', value: 'floating' },
                { label: 'Tag', value: 'tag' },
            ],
        }
    },
    computed: {
        labelCode() {
            return `<semantic-label${this.type ? ' class="'+this.type+'"' : ''}></semantic-label>` // eslint-disable-line
        },
    },
}

</script>



