<template> 
    <example title="Message" :exampleCode="messageCode">
        <semantic-message :type="type" 
                          :dismissable="dismissable">Boo!
        </semantic-message>

       <semantic-divider hidden="true"></semantic-divider>

        <div class="ui form">
            <h4>Types</h4>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Success" v-model="success"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Warning" v-model="warning"></semantic-checkbox>
                </div>
            </div>
            <div class="two fields">
                <div class="field">
                    <semantic-checkbox type="slider" label="Error" v-model="error"></semantic-checkbox>
                </div>
                <div class="field">
                    <semantic-checkbox type="slider" label="Dismissable Icon" v-model="dismissable"></semantic-checkbox>
                </div>
            </div>
        </div>                                 
    </example>
</template>

<script>
import Example from './Example'

export default {
    components: {
        Example,
    },
    data() {
        return {
            name: 'checkbox',
            model: true,
            success: false,
            warning: false,
            error: false,
            small: false,
            large: false,
            dismissable: false,
        }
    },
    computed: {
        messageCode() {
            return `<semantic-message${this.type ? ' type="'+this.type+'"' : ''}${this.dismissable ? ' :dismissable="true"' : ''}><!-- Your Content Here --></semantic-message>` // eslint-disable-line
        },
        type() {
            return `${this.success ? 'success' : ''}${this.warning ? 'warning' : ''}${this.error ? 'error' : ''}` // eslint-disable-line
        },
    },
    watch: {
        success() {
            if (this.success) {
                this.warning = false
                this.error = false
            }
        },
        warning() {
            if (this.warning) {
                this.success = false
                this.error = false
            }
        },
        error() {
            if (this.error) {
                this.warning = false
                this.success = false
            }
        },
    },
}

</script>



